import React from 'react';
import { useDispatch } from 'react-redux';
import { DateRange, updateDateFilter } from '@store/modules/filters';
import { getRelativeDate, getThisSundayDate } from '@utils';
import LineChartPresenter from './Presenter';

type LineChartData = {
  x: number;
  y: number;
};

export interface LineChartSeries {
  data: LineChartData[];
}

interface Props {
  countOfDates: Map<string, number>;
}

function LineChartContainer({ countOfDates }: Props) {
  const dispatch = useDispatch();

  const _updateDateFilter = (date: DateRange) => {
    dispatch(updateDateFilter(date));
  };

  const series: LineChartSeries[] = [{ data: [] }];

  const thisSundayDate = getThisSundayDate();

  for (let week = 0; week < 7; week += 1) {
    let count = 0;

    const sundayDate = getRelativeDate(thisSundayDate, -(week * 7));

    for (let day = 0; day < 7; day += 1) {
      const prevDate = getRelativeDate(thisSundayDate, -(week * 7) + day);

      count += countOfDates.get(prevDate.toDateString()) || 0;
    }

    series[0].data.push({
      x: sundayDate.getTime(),
      y: count,
    });
  }

  return (
    <LineChartPresenter updateDateFilter={_updateDateFilter} series={series} />
  );
}

export default LineChartContainer;
