import React from 'react';
import { useDispatch } from 'react-redux';
import { updateDateFilter } from '@store/modules/filters';
import { DAY_OF_THE_WEEK } from '@constants';
import { color } from '@styles';
import type { DateRange } from '@store/modules/filters';
import { getRelativeDate, getThisSundayDate } from '@utils';
import HeatmapChartPresenter from './Presenter';

const BASE_OPACITY = 0x1f;
const RANGE_SIZE = 2;
const RANGE_COUNT = 4;
const MAX_COUNT = RANGE_SIZE * RANGE_COUNT;

export interface HeatmapRange {
  from: number;
  to: number;
  color: string;
}

export interface HeatmapData {
  x: string;
  y: number;
  meta: {
    count: number;
    date: Date;
  };
}

export interface HeatmapSeries {
  name: string;
  data: HeatmapData[];
}

interface Props {
  countOfDates: Map<string, number>;
}

function HeatmapChartContainer({ countOfDates }: Props) {
  const dispatch = useDispatch();

  const _updateDateFilter = (date: DateRange) => {
    dispatch(updateDateFilter(date));
  };

  /**
   * heatmap에 사용될 데이터를 가공
   */
  const series: HeatmapSeries[] = (() => {
    const series: HeatmapSeries[] = Array.from(Array(7), (_, index) => ({
      name: DAY_OF_THE_WEEK[index],
      data: [],
    }));

    const thisSundayDate = getThisSundayDate();

    for (let day = 0; day < 7; day += 1) {
      const date = getRelativeDate(thisSundayDate, day);

      for (let week = Math.floor(365 / 7); week >= 0; week -= 1) {
        const prevNWeek = getRelativeDate(date, -(7 * week));

        if (prevNWeek.getTime() > Date.now()) {
          continue;
        }

        const fileCount = countOfDates.get(prevNWeek.toDateString()) || 0;

        series[day].data.push({
          /**
           * x label이 보이기 위해서 정확히 몇개의 x값을 가진 항목이 열에 존재해야하는지는 정확히 모르겠음.
           * 요일이 7개이므로 1일부터 7일까지 x값을 주었더니 일단은 제대로 작동함.
           */
          x: prevNWeek.getDate() <= 7 ? `${prevNWeek.getMonth() + 1}월` : '',
          y: fileCount > MAX_COUNT ? MAX_COUNT : fileCount,
          meta: {
            count: fileCount,
            date: prevNWeek,
          },
        });
      }
    }

    series.reverse();

    return series;
  })();

  /**
   * heatmap의 y값이 포함될 범위를 저장하는 배열
   * from ~ to 사이의 값은 해당 color가 적용됨
   */
  const heatmapRanges: HeatmapRange[] = Array(RANGE_COUNT)
    .fill(null)
    .map((_, index) => {
      const times = index + 1;
      const opacity = (
        BASE_OPACITY + Math.floor(((0xff - BASE_OPACITY) / RANGE_COUNT) * times)
      ).toString(16);

      return {
        from: 1 + RANGE_SIZE * (times - 1),
        to: RANGE_SIZE * times,
        color: color.PRIMARY + opacity,
      };
    });

  return (
    <HeatmapChartPresenter
      updateDateFilter={_updateDateFilter}
      heatmapRanges={heatmapRanges}
      series={series}
    />
  );
}

export default HeatmapChartContainer;
