import { graphql } from 'gatsby';
import * as Index from '@components/Pages/Index';

export default Index.default;

export const query = graphql`
  query {
    allFile(
      filter: {
        internal: { mediaType: { regex: "/^(?!(image|font))/" } }
        ext: { ne: ".md" }
      }
    ) {
      nodes {
        name
        relativeDirectory
        ext
        fields {
          stampObject {
            created
          }
        }
      }
    }
    allMarkdownRemark {
      nodes {
        fields {
          slug
        }
        frontmatter {
          title
          subtitle
          tag
        }
      }
    }
  }
`;
