import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { updateKeywordFilter } from '@store/modules/filters';
import { useDebouncedEffect } from '@hooks/useDebouncedEffect';
import SearchIcon from '@assets/images/search.svg';
import { RootState } from '@store/index';
import { SearchBarLayout, SearchBarInput } from './styles';

function SearchBar() {
  const [word, setWord] = useState<string>('');

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setWord(e.target.value);
  };

  /**
   * 렌더링 시 keyword 전역 상태와 동기화
   */
  const keyword = useSelector<RootState, string>(
    (state) => state.filters.keyword
  );

  useEffect(() => {
    setWord(keyword);
  }, []);

  /**
   * 입력을 마쳤을 때 keyword 전역 상태 업데이트
   */
  const dispatch = useDispatch();

  useDebouncedEffect(
    () => {
      dispatch(updateKeywordFilter(word));
    },
    [word],
    1000
  );

  return (
    <SearchBarLayout>
      <SearchIcon />
      <SearchBarInput type="text" value={word} onChange={handleInputChange} />
    </SearchBarLayout>
  );
}

export default SearchBar;
