import React from 'react';
import { useDispatch } from 'react-redux';
import { updateExtFilter } from '@store/modules/filters';
import DonutChartPresenter from './Presenter';

interface Props {
  countOfExts: Map<string, number>;
}

function DonutChartContainer({ countOfExts }: Props) {
  const dispatch = useDispatch();

  const _updateExtFilter = (ext: string) => {
    dispatch(updateExtFilter(ext));
  };

  const labels = [...countOfExts.keys()];
  const series = [...countOfExts.values()];

  const sum = series.reduce((acc, cur) => acc + cur, 0);

  return (
    <DonutChartPresenter
      labels={labels}
      series={series}
      sum={sum}
      updateExtFilter={_updateExtFilter}
    />
  );
}

export default DonutChartContainer;
