export const getRelativeDate = (from: Date = new Date(), num: number): Date => {
  const date = new Date(from);

  return new Date(date.setDate(date.getDate() + num));
};

export const getRelativeMonth = (
  from: Date = new Date(),
  num: number
): Date => {
  const date = new Date(from);

  return new Date(date.setMonth(date.getMonth() + num));
};

export const getRelativeYear = (from: Date = new Date(), num: number): Date => {
  const date = new Date(from);

  return new Date(date.setFullYear(date.getFullYear() + num));
};

export const getThisSundayDate = () => {
  const date = new Date();

  date.setDate(date.getDate() - date.getDay());

  return date;
};

export const getRemovedHMS = (date: Date | number): Date => {
  const tmp = date instanceof Date ? date : new Date(date);

  tmp.setHours(0);
  tmp.setMinutes(0);
  tmp.setSeconds(0);
  tmp.setMilliseconds(0);

  return tmp;
};

export const dateToYYYYMMDD = (date: Date) =>
  `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;

export const dateToKorFormat = (date: Date) =>
  `${date.getFullYear()}년 ${date.getMonth() + 1}월 ${date.getDate()}일`;

export const isDateInScope = (start: Date, end: Date, vs: Date) => {
  if (start > vs || end < vs) {
    return false;
  }

  return true;
};
