import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '@store/index';
import {
  FiltersState,
  updateDateFilter,
  updateExtFilter,
} from '@store/modules/filters';
import { dateToYYYYMMDD } from '@utils';
import { FilterItem, FilterList } from './Styles';

function Filter() {
  const { date, ext } = useSelector<RootState, FiltersState>(
    (state) => state.filters
  );

  const dispatch = useDispatch();

  const dateFilterCloseLabel = (() => {
    if (!date) {
      return null;
    }

    return (
      <FilterItem
        onClick={() => {
          dispatch(updateDateFilter(null));
        }}
      >
        <p>
          {dateToYYYYMMDD(date.start)}
          {date.start.toDateString() !== date.end.toDateString()
            ? ` ~ ${dateToYYYYMMDD(date.end)}`
            : ''}
        </p>
      </FilterItem>
    );
  })();

  const extFilterCloseLabel = (() => {
    if (!ext) {
      return null;
    }

    return (
      <FilterItem
        onClick={() => {
          dispatch(updateExtFilter(null));
        }}
      >
        <p>{ext}</p>
      </FilterItem>
    );
  })();

  return (
    <FilterList>
      {dateFilterCloseLabel}
      {extFilterCloseLabel}
    </FilterList>
  );
}

export default Filter;
