import React from 'react';
import loadable from '@loadable/component';
import { color } from '@styles';
import { DateRange } from '@store/modules/filters';
import { dateToKorFormat, getRelativeDate } from '@utils';
import type { LineChartSeries } from './Container';
import { WeeklyActivityChartLayout } from './Style';

const LoadableChart = loadable(() => import('react-apexcharts'));

interface Props {
  updateDateFilter: (date: DateRange) => void;
  series: LineChartSeries[];
}

function LineChartPresenter({ updateDateFilter, series }: Props) {
  return (
    <WeeklyActivityChartLayout>
      <LoadableChart
        type="line"
        width="100%"
        height="100%"
        series={series}
        options={{
          chart: {
            id: 'fileLineChart',
            toolbar: {
              show: false,
              tools: {
                zoom: false,
                zoomout: false,
                pan: false,
              },
            },
            fontFamily: 'GothicA1',
            events: {
              markerClick: (
                event,
                chartContext,
                { seriesIndex, dataPointIndex }
              ) => {
                const { x } = series[seriesIndex].data[dataPointIndex];

                updateDateFilter({
                  start: new Date(x),
                  end: getRelativeDate(new Date(x), 6),
                });
              },
            },
          },
          stroke: {
            width: 3,
          },
          markers: {
            size: 4,
            colors: ['#ffffff'],
            strokeWidth: 3,
            strokeColors: color.PRIMARY,
            hover: {
              sizeOffset: 2,
            },
          },
          colors: [color.PRIMARY],
          xaxis: {
            tooltip: {
              enabled: false,
            },
            labels: {
              formatter: (value: string) => {
                const date = new Date(value);

                return `${date.getMonth() + 1}/${date.getDate()}`;
              },
            },
            axisTicks: {
              show: false,
            },
          },
          yaxis: {
            show: false,
          },
          dataLabels: {
            enabled: false,
          },
          grid: {
            padding: {
              top: 30,
              left: 30,
              right: 30,
              bottom: 30,
            },
            yaxis: {
              lines: {
                show: false,
              },
            },
            xaxis: {
              lines: {
                show: true,
              },
            },
          },
          tooltip: {
            x: {
              show: false,
            },
            y: {
              formatter: (_, { seriesIndex, dataPointIndex }) => {
                const { x, y } = series[seriesIndex].data[dataPointIndex];

                const startDate = new Date(x);
                const endDate = getRelativeDate(new Date(x), 6);

                return `${dateToKorFormat(startDate)} ~ ${dateToKorFormat(
                  endDate
                )} : ${y}개`;
              },
              title: {
                formatter: () => '',
              },
            },
            marker: {
              show: false,
            },
          },
        }}
      />
    </WeeklyActivityChartLayout>
  );
}

export default LineChartPresenter;
