import React from 'react';
import { useSelector } from 'react-redux';
import { getRemovedHMS, isDateInScope } from '@utils';
import type { FiltersState } from '@store/modules/filters';
import { RootState } from '@store/index';
import ActivityPresenter from './Presenter';

interface Props {
  matchFiles: MatchedFile[];
}

function ActivityContainer({ matchFiles }: Props) {
  const { ext, date, keyword } = useSelector<RootState, FiltersState>(
    (state) => state.filters
  );

  const INCLUDE_KEYWORD = new RegExp(`(${keyword})`, 'i');

  /**
   * 날짜별 포스트 분류
   */
  const divideByDate = (() => {
    const divideByDate = new Map<number, MatchedFile[]>();

    matchFiles.forEach((file) => {
      const {
        relativeDirectory,
        name,
        fields: {
          stampObject: { created },
        },
        markdownData,
      } = file;

      const fileDate = getRemovedHMS(created * 1000);

      const fileExt = file.ext;

      const searchTarget =
        relativeDirectory +
        name +
        fileExt +
        (markdownData?.frontmatter.tag?.join(' ') || '');

      // 확장자, 검색어, 날짜 정보로 필터링
      if (ext && fileExt !== ext) {
        return;
      }

      if (
        date &&
        !isDateInScope(
          getRemovedHMS(date.start),
          getRemovedHMS(date.end),
          fileDate
        )
      ) {
        return;
      }

      if (!INCLUDE_KEYWORD.test(searchTarget)) {
        return;
      }

      // map에 저장
      const msDate = fileDate.getTime();

      if (!divideByDate.has(msDate)) {
        divideByDate.set(msDate, []);
      }

      divideByDate.get(msDate)?.push(file);
    });

    divideByDate.forEach((files) => {
      files.sort(
        (a, b) => a.fields.stampObject.created - b.fields.stampObject.created
      );
    });

    return divideByDate;
  })();

  return (
    <ActivityPresenter
      divideByDate={divideByDate}
      keyword={keyword}
      INCLUDE_KEYWORD={INCLUDE_KEYWORD}
    />
  );
}

export default ActivityContainer;
