import React, { useMemo } from 'react';
import { PageProps } from 'gatsby';
import { useSetTitle } from '@hooks/useSetTitle';
import IndexPresenter from './Presenter';

interface QueryProps {
  allFile: {
    nodes: File[];
  };
  allMarkdownRemark: {
    nodes: MarkdownRemark[];
  };
}

function IndexContainer({
  data: { allFile, allMarkdownRemark },
  pageContext: { repoName },
}: PageProps<QueryProps, PageContext>) {
  useSetTitle(repoName);

  /**
   * 모든 파일에 대해 확장자 없는 상대경로(마크다운 파일의 slug)를 구한 뒤
   * 마크다운 파일이 존재할 경우, 마크다운 frontmatter 데이터를 매칭시켜 저장.
   */
  const matchFiles: MatchedFile[] = useMemo(() => {
    const slugToMarkdown = new Map<string, MarkdownRemark>();

    allMarkdownRemark.nodes.forEach((node) => {
      slugToMarkdown.set(node.fields.slug, node);
    });

    const _matchFiles = allFile.nodes.map((node) => {
      const { relativeDirectory, name } = node;

      const relativePathNoExt =
        relativeDirectory === '' ? name : `${relativeDirectory}/${name}`;

      const mdData = slugToMarkdown.get(relativePathNoExt);

      return {
        ...node,
        markdownData: mdData,
      };
    });

    return _matchFiles;
  }, []);

  /**
   * 확장자 파일 개수 count
   */
  const countOfExts = useMemo(() => {
    const _countOfExts = new Map<string, number>();

    allFile.nodes.forEach(({ ext }) => {
      const count = _countOfExts.get(ext);
      _countOfExts.set(ext, count ? count + 1 : 1);
    });
    return _countOfExts;
  }, []);

  /**
   * 날짜별 파일 개수 count
   */
  const countOfDates = useMemo(() => {
    const _countOfDates = new Map<string, number>();

    allFile.nodes.forEach(({ fields }) => {
      if (!fields.stampObject) {
        return;
      }

      const date = new Date(fields.stampObject.created * 1000);
      const key = date.toDateString();
      const count = _countOfDates.get(key);
      _countOfDates.set(key, count ? count + 1 : 1);
    });

    return _countOfDates;
  }, []);

  return (
    <IndexPresenter
      matchFiles={matchFiles}
      countOfDates={countOfDates}
      countOfExts={countOfExts}
    />
  );
}

export default IndexContainer;
